import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-menu',
  templateUrl: './menu.component.html',
  styleUrls: ['./menu.component.scss']
})
export class MenuComponent implements OnInit {

  menuExpanded = false;
  constructor() { }

  ngOnInit() {
  }

  menuToggle(e) {
    this.menuExpanded = !this.menuExpanded;
  }

}
